'use strict';

(function () {
    angular.module("MainApp", ['ngRoute', 'ngSanitize', 'ngResource', 'ngAnimate', 'mgcrea.ngStrap']);
    angular.module("MainApp").config(function ($routeProvider, $locationProvider) {
        $locationProvider.html5Mode(false).hashPrefix('!');
        $routeProvider.when("/", {
            templateUrl: '/views/index.html', controller: 'mainCtrl'
        }).when("/customers", {
            templateUrl: '/views/customers/customers.html', controller: 'customersCtrl'
        }).otherwise({
            redirectTo: "/"
        });
    });
})();

(function () {
    angular.module("MainApp").controller("globalCtrl", function ($scope) {
        $scope.pageData = [{ Name: 'Home' }, { Name: 'Customers' }];
        $scope.directiveTest = "YO YO Mgular";
    });
})();
(function () {
    angular.module("MainApp").controller('customersCtrl', function ($scope, $modal, customersService) {
        $scope.activePage = 2; //"Customers";
        $scope.readCustomersData = [];
        $scope.createNewCustomers = function () {
            customersService.createNewCustomers($scope.modalData).then(function (result) {
                if (result) {
                    $scope.readCustomersData.push(result.data);
                }
            });
        };
        $scope.modalData = {};
        $scope.readAllCustomers = function () {
            customersService.readAllCustomers().then(function (result) {
                if (result != null) {

                    $scope.readCustomersData = result.data;
                }
            });
        };
        $scope.updateCustomers = function () {
            var id = $scope.currentID;
            var index = $scope.currentIndex;
            customersService.updateCustomers($scope.modalData, id).then(function (result) {
                if (result) {
                    $scope.readCustomersData[index] = $scope.modalData;
                }
            });
        };

        $scope.deleteSample = function () {
            var id = $scope.currentID;
            var index = $scope.currentIndex;
            customersService.deleteSample(id).then(function (result) {
                if (result) {
                    $scope.readCustomersData.splice(index, 1);
                }
            });
        };

        $scope.checkData = function () {
            switch ($scope.modalMode) {
                case "create":
                    $scope.createNewCustomers();
                    break;
                case "edit":
                    $scope.updateCustomers();
                    break;
            }
        };
        $scope.showCustomers = function (type, id, index) {
            $scope.modalMode = type;
            $scope.currentID = id;
            $scope.currentIndex = index;
            var myOtherModal = $modal({ scope: $scope, template: '/views/sample/modal/customersModal.html', show: false });

            switch (type) {
                case 'create':
                    $scope.modalData = {};
                    $scope.modalData.readOnly = false;
                    myOtherModal.$promise.then(myOtherModal.show);
                    break;
                case 'edit':
                    $scope.read(id, function (result) {
                        if (result != "ERROR") {
                            myOtherModal.$promise.then(myOtherModal.show);
                            $scope.modalData = result;
                            $scope.modalData.bCreate = JSON.parse(result.bCreate);
                            $scope.modalData.readOnly = false;
                        }
                    });
                    break;
            }
        };
    });
})();
(function () {
    angular.module("MainApp").factory('customersService', function ($http, $q) {
        var x = {};
        x.readAllCustomers = function () {
            var defer = $q.defer();
            $http({
                method: 'GET',
                url: '/customers',
                contentType: 'application/json; charset=utf-8',
                dataType: 'json'
            }).then(function (result) {
                defer.resolve(result);
            }).catch(function (result) {
                defer.reject(result);
            });
            return defer.promise;
        };

        x.createNewCustomers = function (obj) {
            var defer = $q.defer();
            $http({
                method: "POST",
                url: '/customers/saveadd',
                contentType: 'application/json; charset=utf-8',
                data: {
                    Name: obj.name,
                    Address: obj.address,
                    Email: obj.email,
                    Phone: obj.phone
                },
                dataType: 'json'
                // headers: {
                //     'RequestVerificationToken': token
                // }
            }).then(function (data) {
                defer.resolve(data);
            }, function (data) {
                defer.reject(data);
            });
            return defer.promise;
        };

        x.updateCustomers = function (obj, id) {
            var defer = $q.defer();
            $http({
                method: "POST",
                url: '/save_edit/' + id,
                data: {
                    Name: obj.name,
                    Address: obj.address,
                    Email: obj.email,
                    Phone: obj.phone
                },
                contentType: 'application/json; charset=utf-8',
                dataType: 'json'
                // headers: {
                //     'RequestVerificationToken': token
                // }
            }).then(function (data) {
                defer.resolve(data);
            }, function (data) {
                defer.reject(data);
            });
            return defer.promise;
        };

        x.deleteCustomers = function (id) {
            var defer = $q.defer();
            $http({
                method: 'GET',
                url: '/delete_customer/' + id,
                contentType: 'application/json; charset=utf-8',
                dataType: 'json'
                // headers: {
                //     'RequestVerificationToken': token
                // }
            }).then(function (data) {
                defer.resolve(data);
            }, function (data) {
                defer.reject(data);
            });
            return defer.promise;
        };

        return x;
    });
})();

(function () {
    angular.module("MainApp").controller('mainCtrl', function ($scope) {
        $scope.activePage = 1; // Home     
    });
})();